import React, { Component } from 'react';
import bannerimg from '../../img/banner1.jpg';
import {Link} from 'react-router-dom'; 
import data from '../../data/bannerHome.json';

class Banner extends Component {

  render() {
    
    return (
        <div className="App-banner">
            <div className="container">
              <div className="banner-content" style={{position: 'absolute', top: '100px', width: '50%', minWidth: '420px', padding: '18px 58px'}}>
                <h1>{data[0].headline1}</h1>
                <p>{data[0].headline2}</p>
                <Link className="button" to={data[0].destination}>{data[0].button}</Link>
              </div>
            </div>
            <img src={bannerimg} alt="banner" style={{width: '100%', height: '430px', objectFit: 'cover'}} />
        </div>
    );
  }
}

export default Banner;



